.home-intro {
	height: 100vh !important;
    overflow: hidden;
}

.home-intro-container {
    width: 100%;
    overflow: hidden;
    background-position: center;
    height: auto;
}

.home-intro-container .overlay {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    z-index: 2;
    background: black;
    opacity: 0.6;
}

.home-intro-image {
    width: 100%;
}

.home-intro-content {
    position: absolute;
    top: 15vh;
    left: 10vw;
    z-index: 100;
}

.home-intro-content > h1 > strong {
    font-size: 60px;
    color: white;
}

.home-intro-content > h2 {
    color: white;
}

.center-align-text {
    text-align: center !important;
}

.custom_yellow {
	color : #FFFF00	;
}

.custom_blue {
	color : #3abcc1;
}

.sidebar {
	background: linear-gradient(to right, #494964, #6f6f89);
  position: "fixed" !important;
  width: 19%;
  z-index: 1;
}

.social-icons {
  list-style: none;
  padding: 0;
  margin: 0;
}
.social-icons li {
  display: inline-block;
  font-size: 16px;
  margin-right: 10px;
}
.social-icons li a {
  color: #fff;
  display: inline-block;
  padding: 6px;
  background-color: #3d3d3d;
  border-radius: 100px;
}
.social-icons li a:before {
  width: 16px;
  height: 16px;
  display: inline-block;
  text-align: center;
}


.social-icons .icons,
.social-icons :before,
.social-icons .icon .fa {
  transition: all 0.35s;
  transition-timing-function: cubic-bezier(0.31, -0.105, 0.43, 1.59);
}
.social-icons .icon:before {
  top: 90%;
  left: -110%;
}
.social-icons .icon .fa {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
.social-icons .icon.facebook:before {
  background-color: #3b5998;
}
.social-icons .icon.facebook .fa {
  color: #3b5998;
}

.social-icons .icon.linkedin:before {
  background-color: #3b5998;
}
.social-icons .icon.linkedin .fa {
  color: #3b5998;
}

.social-icons .icon.twitter:before {
  background-color: #3cf;
}
.social-icons .icon.twitter .fa {
  color: #3cf;
}
.social-icons .icon.youtube:before {
  background-color: #dc4a38;
}
.social-icons .icon.youtube .fa {
  color: #dc4a38;
}
.social-icons .icon.dribbble:before {
  background-color: #f26798;
}
.social-icons .icon.dribbble .fa {
  color: #f26798;
}
.social-icons .icon.skype:before {
  background-color: #00aff0;
}
.social-icons .icon.skype .fa {
  color: #00aff0;
}
.social-icons .icon:focus:before,
.social-icons .icon:hover:before {
  top: -10%;
  left: -10%;
}
.social-icons .icon:focus .fa,
.social-icons .icon:hover .fa {
  color: #fff;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.social-icons {
  height: 90px;
  margin: auto;
  font-size: 0;
  text-align: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.social-icons .icon {
  display: inline-block;
  background-color: #fff;
  width: 60px;
  height: 60px;
  line-height: 60px;
  margin: 0 6px;
  text-align: center;
  position: relative;
  overflow: hidden;
  border-radius: 28%;
  box-shadow: 0 3px 10px -3px rgba(0,0,0,0.1);
  opacity: 0.99;
}
.social-icons .icon:before {
  content: '';
  width: 120%;
  height: 120%;
  position: absolute;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.social-icons .icon .fa {
  font-size: 25px;
  vertical-align: middle;
}
.pc-container{
  background: #f1f1f1;
    box-shadow: 2px 7px 20px 8px rgba(0, 0, 0, 0.4);
}
.pc-container .pc-button.pc-button-cancel {
    color: #989898;
}

.bg-color {
  background-color: black;
}

.custom-font {
  font-size: 16px;
}

.ui .segment {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.font-color-white{
  color : white !important;
}

.active {
  background-color: #e6e6e6
}

.link-color{
  color: #fff;
}

.main-container {
  margin-left: 19%;
  position: relative;
}

.menu-item{
  font-size: 24px;
  font-weight: bold;
}

.portfolio {
  margin-left: 2%;
}
.contact {
  margin: 0 2%;
}
.topnav{
  display: none;
}
@media only screen and (max-width: 600px) {
  .sidebar {
    display: none;
  }
  .main-container {
    margin-left: 0%;
    position: relative;
  }
  .ui .segment {
    width : 100%;
  }
  
  .mobile-container {
    max-width: 480px;
    margin: auto;
    background-color: #555;
    height: 500px;
    color: white;
    border-radius: 10px;
  }
  
  .topnav {
    overflow: hidden;
    background-color: #333;
    position: relative;
  }
  
  .topnav #myLinks {
    display: none;
  }
  
  .topnav a {
    color: white;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    display: block;
  }
  
  .topnav a.icon {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
  }
  
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .active {
    background: linear-gradient(to right, #494964, #6f6f89);
    color: white;
  }

  .home-intro-image {
    display: none !important;
  }
  .home-intro-heading{
    font-size: 42px !important;
  }
  .home-intro-description{
    margin-top: 20%;
    font-size: 18px !important;
  }
  .home-intro-description-2{
    font-size: 18px !important;
  }
  .home-intro {
    background: rgb(2,0,36);
    background: linear-gradient(72deg, rgba(2,0,36,1) 0%, rgba(141,141,255,1) 53%, rgba(0,66,0,1) 97%, rgba(13,108,10,1) 100%);
  }
}